<template>
  <p @click="toggleKey" :class="{hide: key_interaction == true}"> Zugeordnet mit der deutschen Tastatur </p>
  <img @click="toggleKey" v-if="show_key" src="https://toreknipping.com/assets/img/key_anleitung.png">
  
  <p id="copyright" :class="{hide: key_interaction == true}"> 
    The developer and owner of this website do not claim any rights or the ownership of scenes that are presented. <br>
    Shown clips are intended for entertainment only. All rights are reserved by the respective owner. <br>
    In case of any issue, please contact toreknipping@toreknipping.com <br> 
  </p>
  <!-- <Instruction/> -->
  <KeyInteraction v-if="key_interaction" :theme="key_.alph" :count="key_.count" :list="eingabe_list"/>
  <input id="userInput" type="text" v-model="eingabe" @keypress="playSound" onblur="this.focus()" autofocus>
  <transition-group name="fade">
    <span v-for="e in eingabe_list" :key="e"> {{ e }} </span>
  </transition-group>
</template>

<script>
import Instruction from './components/Instruction.vue'
import KeyInteraction from './components/KeyInteraction.vue'

export default {
  name: 'App',
  components: { KeyInteraction, Instruction },
  data() {
    return {
      show_key: false,
      key_interaction: false,
      eingabe: '',
      key_: { alph: '', count: 0 },
      eingabe_list: [],
      audio_list_drums: [
          { alph: 'a', src: 'https://toreknipping.com/assets/audio/kick1.mp3'},
          { alph: 's', src: 'https://toreknipping.com/assets/audio/snare.mp3'},
          { alph: 'c', src: 'https://toreknipping.com/assets/audio/snap.mp3'},
          { alph: 'd', src: 'https://toreknipping.com/assets/audio/clap_phat.mp3'},
          { alph: 'e', src: 'https://toreknipping.com/assets/audio/hihat2.mp3'},
          { alph: 'q', src: 'https://toreknipping.com/assets/audio/kick_loop.mp3'},
          { alph: ' ', src: 'https://toreknipping.com/assets/audio/vocal.mp3'},
          { alph: 'v', src: 'https://toreknipping.com/assets/audio/vocal_short.mp3'},
          { alph: 'w', src: 'https://toreknipping.com/assets/audio/hihat.mp3'},
          { alph: 'x', src: 'https://toreknipping.com/assets/audio/clap_phaser2.mp3'},
          { alph: 'y', src: 'https://toreknipping.com/assets/audio/clap_phaser.mp3'},        
      ],
      audio_list_keys: [
          { alph: 'r', count: 0},
          { alph: 't', count: 0},
          { alph: 'z', count: 0},
          { alph: 'u', count: 0},
          { alph: 'i', count: 0},
          { alph: 'o', count: 0},
          { alph: 'p', count: 0},
          { alph: 'ü', count: 0},
          { alph: '+', count: 0},
          { alph: '#', count: 0},
          { alph: 'f', count: 0},
          { alph: 'g', count: 0},
          { alph: 'h', count: 0},
          { alph: 'j', count: 0},
          { alph: 'k', count: 0},
          { alph: 'l', count: 0},
          { alph: 'ö', count: 0},
          { alph: 'ä', count: 0},
          { alph: 'b', count: 0},
          { alph: 'n', count: 0},
          { alph: 'm', count: 0},
      ],
    }
  },
  methods: {
    toggleKey() {
      this.show_key = !this.show_key
    },
    playSound(e) {
      var user_input = e.key.toUpperCase()
      console.log(e.key)

      // key
      var index = this.audio_list_keys.map(function(element) {return element.alph.toUpperCase()}).indexOf(user_input)
      if (index != -1) {
        this.key_.alph = user_input
        this.key_interaction = true  
        this.eingabe_list.push(this.key_.alph)
        this.audio_list_keys[index].count++
        this.key_.count = this.audio_list_keys[index].count
        this.eingabe = ''
      } else {
        // drums
        var index = this.audio_list_drums.map(function(element) {return element.alph.toUpperCase()}).indexOf(user_input)
        if(index != -1) {
          this.drum_interaction = true
          this.eingabe_list.push(user_input)
          var audio = new Audio(this.audio_list_drums[index].src)
          audio.play()
          this.eingabe = ''
        } else {
          this.eingabe = ''
        }
      } 
    }
  },
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: white;
  margin-top: 60px;
}

body {
  background-color: black;
  color: white;
}

#userInput {
  background-color: rgba(0,0,0,0);
  width: 100%;
  box-sizing: content-box;
  border: none;
  resize: none;
  text-align: start;
  margin: 20px;
}

*:focus {
  outline: none !important;
}

#copyright {
  position: fixed;
  top: 90%;
  left: 50%;
  color: gray;
  text-align: end;
  font-size: 30%;
}

#showInput {
  font-size: 30px;
  margin: 2px;
  width: 70%;
}

img {
  width: 70%;
  height: 30%;
}

.fade-enter-from {
  opacity: 0;
}

.fade-enter-to {
  opacity: 1;
}

.fade-enter-active {
  transition: all 2s ease;
}

.fade-leave-from {
  opacity: 1;
}

.fade-leave-to {
  opacity: 0;
}

.fade-leave-active {
  transition: all 2s ease;
}

.hide {
  opacity: 0;
}

</style>
