<template>

    <!-- <span v-for="e in instruction" :key="e" class="instructions"> {{ e }} </span> -->
    <!-- {{ showAlphabet() }} -->
    <span v-for="e in instruction" :key="e" class="instructions"> {{ e }} </span>
    <!-- {{ showInstruction() }} -->
    
</template>

<script>
export default {
    data() {
        return {
            alph: '',
            instruction: [
                'q', 'w', 'e', 'a', 's', 'd', 'y', 'x', 'c', 'v',
                'r', 't', 'z', 'u', 'i', 'o', 'p', 'ü', '+', '#',
                'f', 'g', 'h', 'j', 'k', 'l', 'ö', 'ä',
                'b', 'n', 'm', ' '
            ],
            // makeDivs: `
            //     <div class="instructions"> {{ e }} </div>
            // `
        }
    },
    methods: {
        getRandomNumber(min, max) {
            return Math.random() * (max - min) + min
        },
        showAlphabet() {
            // var alphabets = document.getElementsByClassName("instructions")

            // var winWidth = window.innerWidth;
            // var winHeight = window.innerHeight;

            // for (var i = 0; i < alphabets.length; i++) {
            //     var thisAlphabet = alphabets[i]
            //     console.log(thisAlphabet)

            //     var randomTop = this.getRandomNumber(0, winHeight)
            //     var randomLeft = this.getRandomNumber(0, winWidth)

            //     thisAlphabet.style.top = randomTop + 'px'
            //     thisAlphabet.style.left = randomLeft + 'px'
            // }
            const self = this
            var alphabets = document.getElementsByClassName("instructions")

            var winWidth = window.innerWidth;
            var winHeight = window.innerHeight;

            for (var i = 0; i < alphabets.length; i++) {
                var thisAlphabet = alphabets[i]
                console.log(thisAlphabet)

                var randomTop = self.getRandomNumber(0, winHeight)
                var randomLeft = self.getRandomNumber(0, winWidth)

                thisAlphabet.style.top = randomTop + 'px'
                thisAlphabet.style.left = randomLeft + 'px'
            }
        },
        showInstruction() {
            const self = this
            setInterval(function() {self.showAlphabet()}, 3000)
        },
    },
    // created: function() {
    //     this.showInstruction()
    // }
}
</script>

<style scoped>
    .instructions {
        position: absolute;
        top: 100px;
        left: 100px;
        color: yellow;    
    }

    .fade-enter-from {
        opacity: 0;
    }

    .fade-enter-to {
        opacity: 1;
    }

    .fade-enter-active {
        transition: all 2s ease;
    }

    .fade-leave-from {
        opacity: 1;
    }

    .fade-leave-to {
        opacity: 0;
    }

    .fade-leave-active {
        transition: all 2s ease;
    }
</style>